    background-color:  rgba(0, 0, 0, 0.788);
    height: auto;
    font-family: bitchesBrew, serif, cursive;
  }
  
  a {
    color: #5bd9ff;
    font-size: 2.6rem;
    text-decoration: none;
  }
  
  a:hover {
    color: rgb(107, 220, 255);
